<template>
  <div>
    <h1>Список</h1>

    <div class="row">
      <div class="input-field col s6 ">
        <select ref="select" v-model="filter">
          <option value="" disabled selected>Выбрать статус</option>
          <option value="active">Активные</option>
          <option value="outdated">Просроченные</option>
          <option value="completed">Выполненные</option>
        </select>
        <label>Фильтр статуса</label>
      </div>
    </div>

    <button v-if="filter" class="btn btn-small red" @click="filter = null">Очистить фильтр</button>

    <hr>

    <table v-if="tasks.length">
      <thead>
      <tr>
        <th>#</th>
        <th>Название</th>
        <th>Дата</th>
        <th>Описание</th>
        <th>Статус</th>
        <th></th>
      </tr>
      </thead>
      <tbody>
      <tr
          v-for="(task, idx) of displayTasks"
          :key="task.id"
      >
        <td>{{idx + 1}}</td>
        <td>{{task.title}}</td>
        <td>{{new Date(task.date).toLocaleDateString()}}</td>
        <td class="td">
          <div class="text">{{task.description}}</div>
        </td>
        <td>{{task.status}}</td>
        <td>
          <router-link tag="button" style="margin-right: 1rem;" class="btn btn-small" :to="'/task/' + task.id">
            Редактировать
          </router-link>
          <button class="btn btn-small red" @click="deleteTask(task.id)">Х</button>
        </td>
      </tr>
      </tbody>
    </table>
    <p v-else>Задач нет</p>
  </div>
</template>

<script>
export default {
  data: () => ({
    filter: null,
  }),
  computed: {
    tasks() {
      return this.$store.getters.tasks
    },
    displayTasks() {
      return this.tasks.filter(t => {
        if (!this.filter) {
          return true
        }
        return t.status === this.filter
      })
    }
  },
  methods: {
    deleteTask(id) {
      this.$store.dispatch('deleteTask', id)
      this.$router.push('/list')
    }
  },
  mounted() {
    M.FormSelect.init(this.$refs.select)
  }
}
</script>

<style lang="scss" scoped>
  .td {
    max-width: 400px;
  }

  .text {
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
  }
</style>